import React from 'react';
import profileImage from '../assets/profile.webp';
import instaImage from '../assets/insta.webp';
import tiktokImage from '../assets/tiktok.webp';
import youtubeImage from '../assets/youtube.webp';
import websiteImage from '../assets/website.webp';

// Social media logo components
const InstagramLogo = () => (
  <svg width="20" height="20" viewBox="0 0 24 24" fill="currentColor">
    <path d="M12 2.163c3.204 0 3.584.012 4.85.07 3.252.148 4.771 1.691 4.919 4.919.058 1.265.069 1.645.069 4.849 0 3.205-.012 3.584-.069 4.849-.149 3.225-1.664 4.771-4.919 4.919-1.266.058-1.644.07-4.85.07-3.204 0-3.584-.012-4.849-.07-3.26-.149-4.771-1.699-4.919-4.92-.058-1.265-.07-1.644-.07-4.849 0-3.204.013-3.583.07-4.849.149-3.227 1.664-4.771 4.919-4.919 1.266-.057 1.645-.069 4.849-.069zm0-2.163c-3.259 0-3.667.014-4.947.072-4.358.2-6.78 2.618-6.98 6.98-.059 1.281-.073 1.689-.073 4.948 0 3.259.014 3.668.072 4.948.2 4.358 2.618 6.78 6.98 6.98 1.281.058 1.689.072 4.948.072 3.259 0 3.668-.014 4.948-.072 4.354-.2 6.782-2.618 6.979-6.98.059-1.28.073-1.689.073-4.948 0-3.259-.014-3.667-.072-4.947-.196-4.354-2.617-6.78-6.979-6.98-1.281-.059-1.69-.073-4.949-.073zm0 5.838c-3.403 0-6.162 2.759-6.162 6.162s2.759 6.163 6.162 6.163 6.162-2.759 6.162-6.163c0-3.403-2.759-6.162-6.162-6.162zm0 10.162c-2.209 0-4-1.79-4-4 0-2.209 1.791-4 4-4s4 1.791 4 4c0 2.21-1.791 4-4 4zm6.406-11.845c-.796 0-1.441.645-1.441 1.44s.645 1.44 1.441 1.44c.795 0 1.439-.645 1.439-1.44s-.644-1.44-1.439-1.44z"/>
  </svg>
);

const TikTokLogo = () => (
  <svg width="20" height="20" viewBox="0 0 24 24" fill="currentColor">
    <path d="M12.525.02c1.31-.02 2.61-.01 3.91-.02.08 1.53.63 3.09 1.75 4.17 1.12 1.11 2.7 1.62 4.24 1.79v4.03c-1.44-.05-2.89-.35-4.2-.97-.57-.26-1.1-.59-1.62-.93-.01 2.92.01 5.84-.02 8.75-.08 1.4-.54 2.79-1.35 3.94-1.31 1.92-3.58 3.17-5.91 3.21-1.43.08-2.86-.31-4.08-1.03-2.02-1.19-3.44-3.37-3.65-5.71-.02-.5-.03-1-.01-1.49.18-1.9 1.12-3.72 2.58-4.96 1.66-1.44 3.98-2.13 6.15-1.72.02 1.48-.04 2.96-.04 4.44-.99-.32-2.15-.23-3.02.37-.63.41-1.11 1.04-1.36 1.75-.21.51-.15 1.07-.14 1.61.24 1.64 1.82 3.02 3.5 2.87 1.12-.01 2.19-.66 2.77-1.61.19-.33.4-.67.41-1.06.1-1.79.06-3.57.07-5.36.01-4.03-.01-8.05.02-12.07z"/>
  </svg>
);

const YouTubeLogo = () => (
  <svg width="20" height="20" viewBox="0 0 24 24" fill="currentColor">
    <path d="M23.498 6.186a3.016 3.016 0 0 0-2.122-2.136C19.505 3.545 12 3.545 12 3.545s-7.505 0-9.377.505A3.017 3.017 0 0 0 .502 6.186C0 8.07 0 12 0 12s0 3.93.502 5.814a3.016 3.016 0 0 0 2.122 2.136c1.871.505 9.376.505 9.376.505s7.505 0 9.377-.505a3.015 3.015 0 0 0 2.122-2.136C24 15.93 24 12 24 12s0-3.93-.502-5.814zM9.545 15.568V8.432L15.818 12l-6.273 3.568z"/>
  </svg>
);

const Links = () => {
  const [isMobile, setIsMobile] = React.useState(window.innerWidth <= 768);
  const [showBanner, setShowBanner] = React.useState(false);
  const [showProfile, setShowProfile] = React.useState(false);
  const [visibleLinks, setVisibleLinks] = React.useState([]);
  const [isSocialBrowser, setIsSocialBrowser] = React.useState(false);

  // Detect social media browsers and handle URL encoding issues
  React.useEffect(() => {
    const userAgent = navigator.userAgent.toLowerCase();
    const isInstagram = userAgent.includes('instagram');
    const isFacebook = userAgent.includes('fban') || userAgent.includes('fbav');
    const isTwitter = userAgent.includes('twitter');
    
    if (isInstagram || isFacebook || isTwitter) {
      setIsSocialBrowser(true);
    }

    // Fix Instagram's URL encoding issue with hash symbols
    if (isInstagram && window.location.hash.includes('%23')) {
      const decodedHash = decodeURIComponent(window.location.hash);
      if (decodedHash !== window.location.hash) {
        window.location.hash = decodedHash;
      }
    }
  }, []);

  React.useEffect(() => {
    const handleResize = () => {
      setIsMobile(window.innerWidth <= 768);
    };

    window.addEventListener('resize', handleResize);
    return () => window.removeEventListener('resize', handleResize);
  }, []);

  React.useEffect(() => {
    // Reset states first
    setShowBanner(false);
    setShowProfile(false);
    setVisibleLinks([]);
    
    // Staggered animations with small delay to ensure fade-in is visible
    setTimeout(() => setShowBanner(true), 100);
    setTimeout(() => setShowProfile(true), 300);
    
    // Staggered link animations
    setTimeout(() => setVisibleLinks([0]), 450);
    setTimeout(() => setVisibleLinks([0, 1]), 600);
    setTimeout(() => setVisibleLinks([0, 1, 2]), 750);
    setTimeout(() => setVisibleLinks([0, 1, 2, 3]), 900);
  }, []);
  const socialLinks = [
    {
        name: 'Cinestoke.com',
        url: '/',
        icon: '',
        color: '#FFFFFF',
        image: websiteImage
    },
    {
      name: 'Instagram',
      url: 'https://www.instagram.com/jaronwenger',
      icon: '',
      color: '#E4405F',
      image: instaImage,
      logo: <InstagramLogo />
    },
    {
      name: 'YouTube',
      url: 'https://www.youtube.com/@jaronwenger',
      icon: '',
      color: '#FF0000',
      image: youtubeImage,
      logo: <YouTubeLogo />
    },
    {
      name: 'TikTok',
      url: 'https://www.tiktok.com/@jaronwenger',
      icon: '',
      color: '#000000',
      image: tiktokImage,
      logo: <TikTokLogo />
    }
  ];

  const handleLinkClick = (url) => {
    if (url.startsWith('http')) {
      window.open(url, '_blank');
    } else {
      window.location.href = url;
    }
  };

  // Simple fallback for social media browsers
  if (isSocialBrowser) {
    return (
      <div style={{
        backgroundColor: 'black',
        color: 'white',
        minHeight: '100vh',
        width: '100vw',
        maxWidth: '100vw',
        overflow: 'hidden',
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
        justifyContent: 'center',
        padding: '20px',
        boxSizing: 'border-box',
        fontFamily: 'Bebas Neue, Impact, Arial Narrow Bold, sans-serif'
      }}>
        <h1 style={{
          fontSize: isMobile ? '3.5rem' : '6rem',
          color: 'white',
          textShadow: '0 0 10px rgba(255, 255, 255, 0.2), 0 0 20px rgba(255, 255, 255, 0.2), 0 0 30px rgba(255, 255, 255, 0.2), 0 0 40px rgba(255, 255, 255, 0.2)',
          margin: 0,
          letterSpacing: '0.05em',
          marginBottom: '2rem',
          textAlign: 'center',
          wordWrap: 'break-word',
          maxWidth: '100%',
          opacity: showBanner ? 1 : 0,
          transform: showBanner ? 'translateY(0)' : 'translateY(20px)',
          transition: 'opacity 0.5s ease, transform 0.5s ease'
        }}>
          C I N E S T O K E
        </h1>
        
        <img 
          src={profileImage} 
          alt="Profile"
          loading="eager"
          decoding="sync"
          style={{
            width: '80px',
            height: '80px',
            borderRadius: '50%',
            objectFit: 'cover',
            boxShadow: '0 4px 15px rgba(0, 0, 0, 0.3)',
            marginBottom: '1rem',
            opacity: showProfile ? 1 : 0,
            transform: showProfile ? 'translateY(0)' : 'translateY(20px)',
            transition: 'opacity 0.5s ease, transform 0.5s ease'
          }}
        />
        
        <div style={{
          fontSize: '1.2rem',
          fontWeight: 'bold',
          color: 'white',
          marginBottom: '0.5rem',
          opacity: showProfile ? 1 : 0,
          transform: showProfile ? 'translateY(0)' : 'translateY(20px)',
          transition: 'opacity 0.5s ease, transform 0.5s ease',
          visibility: showProfile ? 'visible' : 'hidden'
        }}>
          @jaronwenger
        </div>
        
        <div style={{
          fontSize: '0.9rem',
          color: '#cccccc',
          marginBottom: '2rem',
          fontFamily: 'Georgia, serif',
          fontStyle: 'italic',
          opacity: showProfile ? 1 : 0,
          transform: showProfile ? 'translateY(0)' : 'translateY(20px)',
          transition: 'opacity 0.5s ease, transform 0.5s ease',
          visibility: showProfile ? 'visible' : 'hidden'
        }}>
          making dreams come true
        </div>

        <div style={{
          maxWidth: '400px',
          width: '90%',
          display: 'flex',
          flexDirection: 'column',
          gap: '1rem',
          alignItems: 'center',
          opacity: visibleLinks.length > 0 ? 1 : 0,
          transform: visibleLinks.length > 0 ? 'translateY(0)' : 'translateY(20px)',
          transition: 'opacity 0.5s ease, transform 0.5s ease'
        }}>
          {socialLinks.map((link, index) => (
            <a
              key={index}
              href={link.url}
              target="_blank"
              rel="noopener noreferrer"
              style={{
                backgroundColor: 'rgba(255, 255, 255, 0.1)',
                border: '1px solid rgba(255, 255, 255, 0.2)',
                borderRadius: '12px',
                padding: '16px 20px',
                color: 'white',
                fontSize: '1.1rem',
                fontWeight: '500',
                textDecoration: 'none',
                textAlign: 'center',
                display: 'flex',
                alignItems: 'center',
                justifyContent: 'space-between',
                backdropFilter: 'blur(10px)',
                letterSpacing: '1px',
                width: '100%',
                outline: 'none',
                borderStyle: 'solid',
                userSelect: 'none',
                WebkitUserSelect: 'none',
                MozUserSelect: 'none',
                msUserSelect: 'none',
                position: 'relative',
                opacity: visibleLinks.includes(index) ? 1 : 0,
                transform: visibleLinks.includes(index) ? 'translateY(0)' : 'translateY(20px)',
                transition: 'opacity 0.5s ease, transform 0.5s ease'
              }}
            >
              {link.image && (
                <img 
                  src={link.image} 
                  alt={link.name}
                  loading="eager"
                  decoding="sync"
                  style={{
                    width: '50px',
                    height: '50px',
                    borderRadius: '4px',
                    objectFit: 'cover',
                    marginLeft: '-10px',
                    marginTop: '-8px',
                    marginBottom: '-8px'
                  }}
                />
              )}
              <div style={{
                display: 'flex',
                alignItems: 'center',
                justifyContent: 'center',
                position: 'absolute',
                left: '0',
                right: '0',
                textAlign: 'center'
              }}>
                <span>{link.name}</span>
                {link.logo && (
                  <span style={{ marginLeft: '8px', marginTop: '4px' }}>
                    {link.logo}
                  </span>
                )}
              </div>
              <span style={{
                fontSize: '1.2rem',
                opacity: 0.7,
                marginLeft: 'auto'
              }}>
                
              </span>
            </a>
          ))}
        </div>
      </div>
    );
  }

  return (
    <div style={{
      backgroundColor: 'black',
      color: 'white',
      minHeight: '100vh',
      width: '100%',
      display: 'flex',
      flexDirection: 'column',
      alignItems: 'center',
      fontFamily: 'Bebas Neue, Impact, Arial Narrow Bold, sans-serif'
    }}>
      {/* CINESTOKE Text */}
      <div style={{
        marginBottom: '4rem',
        marginTop: '2rem',
        textAlign: 'center',
        opacity: showBanner ? 1 : 0,
        transform: showBanner ? 'translateY(0)' : 'translateY(20px)',
        transition: 'opacity 0.5s ease, transform 0.5s ease'
      }}>
        <h1 style={{
          fontSize: isMobile ? '3.5rem' : '6rem',
          color: 'white',
          textShadow: '0 0 10px rgba(255, 255, 255, 0.2), 0 0 20px rgba(255, 255, 255, 0.2), 0 0 30px rgba(255, 255, 255, 0.2), 0 0 40px rgba(255, 255, 255, 0.2)',
          margin: 0,
          letterSpacing: '0.05em'
        }}>
          C I N E S T O K E
        </h1>
      </div>

      {/* Header */}

      {/* Profile Image */}
      <div style={{
        marginBottom: '2rem',
        marginTop: isMobile ? '-2rem' : '-2rem',
        textAlign: 'center',
        opacity: showProfile ? 1 : 0,
        transform: showProfile ? 'translateY(0)' : 'translateY(20px)',
        transition: 'opacity 0.5s ease, transform 0.5s ease'
      }}>
        <img 
          src={profileImage} 
          alt="Profile"
          style={{
            width: '80px',
            height: '80px',
            borderRadius: '50%',
            objectFit: 'cover',
            boxShadow: '0 4px 15px rgba(0, 0, 0, 0.3)',
            marginBottom: '10px'
          }}
        />
        <div style={{
          fontSize: '1.2rem',
          fontWeight: 'bold',
          color: 'white',
          marginBottom: '5px',
          textTransform: 'lowercase'
        }}>
          @jaronwenger
        </div>
        <div style={{
          fontSize: '0.9rem',
          color: '#cccccc',
          marginBottom: '10px',
          fontFamily: 'Georgia, serif',
          fontStyle: 'italic'
        }}>
          making dreams come true
        </div>
      </div>

      {/* Links Container */}
      <div style={{
        maxWidth: '400px',
        width: '100%',
        display: 'flex',
        flexDirection: 'column',
        gap: '1rem'
      }}>
        {socialLinks.map((link, index) => (
          <button
            key={index}
            onClick={() => handleLinkClick(link.url)}
            style={{
              backgroundColor: 'rgba(255, 255, 255, 0.1)',
              border: '1px solid rgba(255, 255, 255, 0.2)',
              borderRadius: '12px',
              padding: '16px 20px',
              color: 'white',
              fontSize: '1.1rem',
              fontWeight: '500',
              cursor: 'default',
              transition: 'all 0.3s ease',
              display: 'flex',
              alignItems: 'center',
              justifyContent: 'space-between',
              backdropFilter: 'blur(10px)',
              letterSpacing: '1px',
              width: '90%',
              margin: '0 auto',
              textAlign: 'left',
              outline: 'none',
              borderStyle: 'solid',
              userSelect: 'none',
              WebkitUserSelect: 'none',
              MozUserSelect: 'none',
              msUserSelect: 'none',
              opacity: visibleLinks.includes(index) ? 1 : 0,
              transform: visibleLinks.includes(index) ? 'translateY(0)' : 'translateY(20px)',
              transition: 'opacity 0.5s ease, transform 0.5s ease'
            }}
            onFocus={(e) => {
              e.target.style.outline = 'none';
            }}
            onBlur={(e) => {
              e.target.style.outline = 'none';
            }}
          >
            {link.image ? (
              <img 
                src={link.image} 
                alt={link.name}
                style={{
                  width: '50px',
                  height: '50px',
                  borderRadius: '4px',
                  objectFit: 'cover',
                  marginLeft: '-10px',
                  marginTop: '-8px',
                  marginBottom: '-8px'
                }}
              />
            ) : null}
            <div style={{
              display: 'flex',
              alignItems: 'center',
              justifyContent: 'center',
              position: 'absolute',
              left: '0',
              right: '0',
              textAlign: 'center'
            }}>
              <span>{link.name}</span>
              {link.logo && (
                <span style={{ marginLeft: '8px', marginTop: '4px' }}>
                  {link.logo}
                </span>
              )}
            </div>
            <span style={{
              fontSize: '1.2rem',
              opacity: 0.7
            }}>
              
            </span>
          </button>
        ))}
      </div>

      {/* Footer */}
      <div style={{
        marginTop: '3rem',
        textAlign: 'center',
        opacity: 0.6
      }}>
        <p style={{
          fontSize: '0.9rem',
          margin: 0
        }}>

        </p>
      </div>
    </div>
  );
};

export default Links;
